@import "../../definitions";

.article-download-links-list--js {
  @include tooltip-box();
  position: absolute;
  text-align: left;
  top: 80px;
  width: 100vw;
  z-index: 20;

  // Turned off until the position right can be fixed so arrow always appears under download link icon.
  //&:before {
  //  @include arrow("up", 7, $color-text-ui-background--reverse);
  //  position: absolute;
  //  right: calc(6vw + 3.5px);
  //  top: -6px;
  //}

  ul {
    list-style: none;
    padding-left: 0;
  }

  ul:last-child {
    margin-bottom: 0;
  }

  .article-download-links-list__heading {
    @include section-title-typeg();
    margin: 0;
  }

  .article-download-links-list__link {
    color: $color-text--reverse;
    @include nav-page-typeg();
    text-decoration: none;
  }

}

@media only all and (min-width: #{$bkpt-article-download-links-list}px) {

  .article-download-links-list--js {
    border-radius: 3px;
    max-width: 320px;
    right: 0;
    top: 110px;
  }

}
